<template>
  <div id="endPage" v-bind:style="{height: this.endPage.height+'px',width:this.endPage.width+'px'}">
    <div id="endPageImg">
      <p id="winner" v-if="this.haveWinner">
        winner: {{winner[0].tank.name}}
        <br />Used weapon:
        <br />
        Classic {{load[0]}}
        <br />
        Speed {{load[1]}}
        <br />
        mine {{load[2]}}
      </p>
      <p id="noWinner" v-if="!this.haveWinner">No winner!</p>
      <button id="restart" class="endPageButton" v-on:click="reset()">Restart Game!</button>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import resetState from "../../mixins/reset";
export default {
  name: "endPage",
  components: {},
  data() {
    return {};
  },
  methods: {
    reset: function() {
      $("#endPage").fadeOut(800, this.resetState);
    }
  },
  computed: {
    winner: function() {
      return this.$store.state.winner;
    },
    endPage: function() {
      return this.$store.state.endPage;
    },
    load: function() {
      if (this.winner[0].tank.id == 1) {
        return this.$store.state.bullet1.load;
      } else {
        return this.$store.state.bullet2.load;
      }
    },
    haveWinner: function() {
      if (this.winner[0] == null) {
        return false;
      }
      return true;
    }
  },
  mixins: [resetState],
  created: function() {}
};
</script>
<style scoped>
#endPage {
  border-radius: 30px;

  margin: 0 auto;
  width: 1500px;
  height: 1500px;
  position: relative;
  margin-bottom: 10px;
  background-color: rgb(255, 224, 122);
  border-color: black;
  left: 20px;
}
.endPageButton {
  border-radius: 20px;
  position: relative;
  margin: 35% auto;
  height: 20%;
  width: 20%;
}
#endPageImg {
  background-color: rgb(255, 255, 255);
  border-radius: 20px;
  top: 5%;
  position: relative;
  margin: 0 auto;
  height: 90%;
  width: 90%;
}
#winner {
  text-align: left;
  left: -0.5%;
  font-size: 60px;
  font-style: italic;
  position: absolute;
  left: 39%;
}
#noWinner {
  text-align: center;
  font-size: xxx-large;
  font-style: italic;
  position: absolute;
}
</style>